.wordgraph-holder {
    width: 180vw;
    background-color: rgba(0, 0, 0, .05);
    padding: 10px;
}

.hidden {
    visibility: hidden;
}

text {
    cursor: default;
}

/* .node, .link {
    transition: opacity filter 2s ease;
} */

.graph-controls-overlay {
    position: absolute;
    top: -80px;
    right: 0px;
    background: white;
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 12px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
    z-index: 100;
    display: flex;
    gap: 22px;
    min-width: 180px;
    font-family: monospace;
}

.graph-controls-overlay .slider-container,
.graph-controls-overlay .dropdown-container {
    display: flex;
    flex-direction: column;
    font-size: 0.9em;
    position: relative;
}

.graph-controls-overlay .slider-container label {
    cursor: help;
    margin-bottom: 4px;
}

.graph-controls-overlay .slider-container .tooltip {
    visibility: hidden;
    background-color: #333;
    color: white;
    text-align: center;
    padding: 8px;
    border-radius: 6px;
    position: absolute;
    z-index: 1;
    top: 125%;
    left: 50%;
    transform: translateX(-50%);
    width: 200px;
    font-size: 0.9em;
    opacity: 0;
    transition: opacity 0.3s;
}

.graph-controls-overlay .slider-container .tooltip::after {
    content: "";
    position: absolute;
    bottom: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: transparent transparent #333 transparent;
}

.graph-controls-overlay .slider-container label:hover + .tooltip {
    visibility: visible;
    opacity: 1;
}

.graph-controls-overlay .dropdown-container label {
    font-weight: normal;
    white-space: nowrap;
    margin-bottom: 4px;
}

.graph-controls-overlay .dropdown-container select {
    padding: 4px 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
    background: white;
    font-size: 0.9em;
    min-width: 120px;
}